import { css, CSSResultArray, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const flexContainerStyles: GenerateStyles = () => {
    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: flex;
                flex-flow: row nowrap;
                justify-content: space-between;
                align-content: space-between;
                align-items: center;
                gap: var(--padding-main, 16px);
            }

            :host([flow='column']) {
                flex-flow: column nowrap;
            }

            ::slotted(*) {
                margin: 0;
                padding: 0;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
            }

            :host([flex-child='first']) ::slotted(*:first-child) {
                flex: 1;
            }
            :host([flex-child='last']) ::slotted(*:last-child) {
                flex: 1;
            }
        `,
    ] as CSSResultArray;
};
